let counter = 0;
let direction = "RIGHT";
const S = 5;
 

const mapArr = (state, action, dir) => {
  const target = {};
  action.arr.forEach((el) => {
    target[el] = {
      key: state[el].key,
      x: dir === "RIGHT" ? state[el].x + S : state[el].x - S,
      y: state[el].y,
      status: state[el].status,
      angle: state[el].angle,
    };
  });
  return target;
};

export default (state, action) => {
  let target;
  if (direction === "RIGHT") {
    target = { ...state, ...mapArr(state, action, direction) };
    counter++;
  }
  if (direction === "LEFT") {
    target = { ...state, ...mapArr(state, action, direction) };
    counter--;
  }
  if (counter === 12) {
    direction = "LEFT";
  }
  if (counter === 0) {
    direction = "RIGHT";
  }
  return target;
};
