const initialState = {
    logoy: 400,
  };
  
  const logoReducer = (state = initialState,action) => {
 
    switch(action.type){
      case 'logo':
    return { logoy: state.logoy <= 100 ? 100 : state.logoy - 1 };
    default:
    return state
    }
  };
  
  export default logoReducer;
  