const initialState = {
    shipx: 285,
  };
  
  const shipReducer = (state = initialState,action) => {
    
    switch(action.type){
    case 'LEFT':
    return { shipx: state.shipx <= 0 ? 0 : state.shipx - 2};
    case 'RIGHT':
      return { shipx: state.shipx >= 570 ? 570 : state.shipx + 2 };
      default:
        return state

    }
     
  };
  
  export default shipReducer;
  