import React, { PureComponent } from "react";

import "./text.css";

import genBack from "../../../utility/background";

class BackGround extends PureComponent {
  constructor(props) {
    super(props);
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    this.bgLoop();
    // setInterval(music.background,1000)
  }

  bgLoop = () =>{
    this.props.bgmove();
    if (this.aniStatus) {
      requestAnimationFrame(this.bgLoop);
    }
  }

  componentWillUnmount() {
    this.aniStatus = false;
  }

  componentDidUpdate(){
    this.paint()
  }

  paint = () => {
    const { y,  score } = this.props;

    // bgmove();

    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");

      context.fillStyle = "black";
      context.fillRect(0, 0, 600, 400);

      genBack(context, y);
      context.font = "25px myFirstFont";
      context.fillStyle = "rgb(182,49,32)";
      context.fillText("1UP", 100, 30);
      context.fillText("HI-SCORE", 224, 30);
      context.fillText("2UP", 450, 30);
      context.fillStyle = "white";
      context.fillText(score, 150, 50);
      context.fillText("00", 500, 50);

      context.fillStyle = "rgb(22,99,228)";
      context.fillText(score > 5000 ? score : "5000", 280, 50);
    }

    // if (this.aniStatus) {
    //   requestAnimationFrame(this.paint);
    // }
  };

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"backgorund"}
        ref={this.canvasRef}
        width={width}
        height={height}
        style={{ zIndex: -100, position: "fixed", top: 0, left: 0 }}
      />
    );
  }
}

export default BackGround;
