import React, { PureComponent } from "react";
import shipimg from "../../assets/images/ship.jpg";


class Ship extends PureComponent {
  constructor(props) {
    super(props);
    this.keyState = {};
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

 

  componentDidMount() {
    window.addEventListener("keydown", (e) => {
      this.keyState[e.code] = true;
    });
    window.addEventListener("keyup", (e) => {
      this.keyState[e.code] = false;
    });
    this.shipLoop();
    this.paint();
    
     
  }

  componentDidUpdate(){
    this.paint()
  }

  componentWillUnmount(){
    this.aniStatus = false;
  }

  shipLoop = () => {
   
    if (this.keyState["ArrowLeft"] || this.keyState["KeyA"]) {
      this.props.shipMove("LEFT");
    }
    if (this.keyState["ArrowRight"] || this.keyState["KeyD"]) {
      this.props.shipMove("RIGHT");
    }
    if(this.aniStatus){
    requestAnimationFrame(this.shipLoop);
    }
  };


 
  paint = () => {
   
    const { shipx } = this.props;
    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");
      const shipimage = new Image();
      shipimage.src = shipimg;
      shipimage.onload = () => {
        context.clearRect(0,0,600,400);
        context.drawImage(shipimage, shipx, 360, 30, 28);
       
      };
       
    }
  };

  render() {
    
    return (
      
      <canvas
        id = {"ship"}
        ref={this.canvasRef}
        width={this.props.width}
        height={this.props.height}
        style={{ zIndex: 0, position: "fixed", top: 0, left: 0 }}
      />
   
   
    );
  }
}

export default Ship;
