import React, { PureComponent } from "react";
import green2img from "../../assets/images/green2.jpg";
import purple2img from "../../assets/images/purple2.jpg";
import red2img from "../../assets/images/red2.jpg";
import select from "../../utility/select";

class EnemyAttack extends PureComponent {
  constructor(props) {
    super(props);
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    this.paint();
    setTimeout(() => {
      this.props.pickAttacker(5);
    }, 2000);
    setTimeout(() => {
      this.props.pickAttacker(9);
    }, 4000);
    this.enemyAttackHandler();
  }
 

  enemyAttackHandler = () => {
    const ATKINTERVAL = Math.floor(Math.random() * 5000);
    this.arr=[]
    this.props.enemies.forEach((enemy)=>{
       this.arr.push({x:enemy.x,y:enemy.y})
    })
    this.enemyAttackLoop(this.props.enemies.map((el) => el.key));
       
  };

  enemyAttackLoop = arr => {
    this.props.enemyAttack(arr);

    const handler = requestAnimationFrame(
      this.enemyAttackLoop.bind(this, arr)
    );
  };

  paint() {}

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"enemyattack"}
        ref={this.canvasRef}
        width={width}
        height={height}
        style={{ zIndex: 100, position: "fixed", top: 0, left: 0 }}
      />
    );
  }
}
export default EnemyAttack;
