const FREQENCY = 30;
const SIZE = 1;
const NUM = 200;
const COLOR = [
  "rgb(198,118,255)", //purple
  "rgb(0,255,255)", //blue
  "rgb(255,0,255)", //pink
  "rgb(255,0,0)", //red
  "rgb(0,0,255)", //darkblue
  "rgb(255,255,0)", //yellow
  "rgb(255,255,255)"
];
const arrX = [];
const arrY = [];
const arrC = [];
const arrF = [];

function getRanY() {
  return Math.floor(401 * Math.random());
}
function getRanX() {
  return Math.floor(601 * Math.random());
}
function getRanC() {
  return COLOR[Math.floor(COLOR.length * Math.random())];
}
function getRanF() {
  return Math.floor((FREQENCY + 1) * Math.random());
}

for (let i = 0; i <= NUM; i++) {
  arrF.push(getRanF());
}
for (let i = 0; i <= NUM; i++) {
  arrX.push(getRanX());
}
for (let i = 0; i <= NUM; i++) {
  arrY.push(getRanY());
}
for (let i = 0; i <= NUM; i++) {
  arrC.push(getRanC());
}

function draw(a, context, y) {
  for (let i = 0; i < a; i++) {
    if (arrF[i] <= (1 / 2) * FREQENCY) {
      if (
        y % FREQENCY > arrF[i] &&
        y % FREQENCY < arrF[i] + (1 / 2) * FREQENCY
      ) {
        context.fillStyle = arrC[i];
        context.fillRect(
          arrX[i],
          y + arrY[i] > 400 ? y + arrY[i] - 400 : y + arrY[i],
          SIZE,
          SIZE
        );
      }
    }
    if (arrF[i] > (1 / 2) * FREQENCY) {
      if (
        y % FREQENCY > arrF[i] ||
        y % FREQENCY < arrF[i] - (1 / 2) * FREQENCY
      ) {
        context.fillStyle = arrC[i];
        context.fillRect(
          arrX[i],
          y + arrY[i] > 400 ? y + arrY[i] - 400 : y + arrY[i],
          SIZE,
          SIZE
        );
      }
    }
  }
}

export default function (context, y) {
  draw(NUM, context, y);
}
