function select(x, y, ctx, YB, YD, g, p, r) {
  if (y === YB + 3 * YD || y === YB + 4 * YD || y === YB + 5 * YD) {
    ctx.drawImage(g, x, y);
  }
  if (y === YB + 2 * YD) {
    ctx.drawImage(p, x, y);
  }
  if (y === YB + 1 * YD) {
    ctx.drawImage(r, x, y);
  }
}

export default select;
