const initialState = {
  bulletStatus: true,
  bullety: 353,
};

const bulletReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ATTACK":

      return { bulletStatus:true, bullety: state.bullety <= -12 ? 353 : state.bullety - 8};
    case "VANISH":
       
 
      return {bulletStatus:false,bullety:353}
      
    default:
      return state;
  }
};

export default bulletReducer;
