import React, { PureComponent } from "react";
import Auxi from "../../auxi/auxi";
import { connect } from "react-redux";
import Ship from "../../components/glx/ship";
import Bullet from "../../components/glx/bullet";
import Enemy from "../../components/glx/enemy";
import EnemyAttack from "../../components/glx/enemyattack";
import music from "../../utility/music";

class InGame extends PureComponent {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
     music.start();
  }
  componentDidUpdate() {}
  render() {
    /*hold enemy state in an array, used for collision detetion
    and enemymove. the original state is an array because of  
    enemy attack logic*/

    const MOVARR = [];
    for (let i = 0; i <= 45; i++) {
      if (this.props[i]) {
        if (this.props[i].status === "MOVING") {
          MOVARR.push(this.props[i]);
        }
      }
    }
    const ATKARR = [];
    for (let i = 0; i <= 45; i++) {
      if (this.props[i]) {
        if (this.props[i].status === "ATTACKING") {
          ATKARR.push(this.props[i]);
        }
      }
    }
    console.log(this.props);
    return (
      <Auxi>
        <Ship
          width={this.props.width}
          height={this.props.height}
          shipx={this.props.shipx}
          shipMove={this.props.shipMove}
        />
        <Bullet
          width={this.props.width}
          height={this.props.height}
          bullety={this.props.bullety}
          attack={this.props.attack}
          shipx={this.props.shipx}
          enemies={MOVARR}
          vanish={this.props.vanish}
          explode={this.props.explode}
          bulletStatus={this.props.bulletStatus}
          passId={this.passId}
          YB={60}
          YD={25}
          goal={this.props.goal}
          enemyExplode={this.props.enemyExplode}
        />
        <Enemy
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={MOVARR}
          enemyMove={this.props.enemyMove}
        />
        {/* <EnemyAttack
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={ATKARR}
          enemyAttack={this.props.enemyAttack}
          pickAttacker={this.props.pickAttacker}
        /> */}
      </Auxi>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    shipx: state.shipReducer.shipx,
    bullety: state.bulletReducer.bullety,
    bulletStatus: state.bulletReducer.bulletStatus,
    0: state.enemyReducer[0],
    1: state.enemyReducer[1],
    2: state.enemyReducer[2],
    3: state.enemyReducer[3],
    4: state.enemyReducer[4],
    5: state.enemyReducer[5],
    6: state.enemyReducer[6],
    7: state.enemyReducer[7],
    8: state.enemyReducer[8],
    9: state.enemyReducer[9],
    10: state.enemyReducer[10],
    11: state.enemyReducer[11],
    12: state.enemyReducer[12],
    13: state.enemyReducer[13],
    14: state.enemyReducer[14],
    15: state.enemyReducer[15],
    16: state.enemyReducer[16],
    17: state.enemyReducer[17],
    18: state.enemyReducer[18],
    19: state.enemyReducer[19],
    20: state.enemyReducer[20],
    21: state.enemyReducer[21],
    22: state.enemyReducer[22],
    23: state.enemyReducer[23],
    24: state.enemyReducer[24],
    25: state.enemyReducer[25],
    26: state.enemyReducer[26],
    27: state.enemyReducer[27],
    28: state.enemyReducer[28],
    29: state.enemyReducer[29],
    30: state.enemyReducer[30],
    31: state.enemyReducer[31],
    32: state.enemyReducer[32],
    33: state.enemyReducer[33],
    34: state.enemyReducer[34],
    35: state.enemyReducer[35],
    36: state.enemyReducer[36],
    37: state.enemyReducer[37],
    38: state.enemyReducer[38],
    39: state.enemyReducer[39],
    40: state.enemyReducer[40],
    41: state.enemyReducer[41],
    42: state.enemyReducer[42],
    43: state.enemyReducer[43],
    44: state.enemyReducer[44],
    45: state.enemyReducer[45],
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    attack: (shipx) => {
      dispatch({ type: "ATTACK", shipx: shipx });
    },
    shipMove: (DIR) => {
      dispatch({ type: DIR });
    },
    enemyMove: (arr) => {
      dispatch({ type: "ENEMYMOVE", arr: arr });
    },

    vanish: (id) => {
      dispatch({ type: "VANISH", id: id });
    },
    goal: (color) => {
      dispatch({ type: color });
    },
    enemyAttack: (id, x, y) => {
      dispatch({ type: "ENEMYATTACK", id: id, x: x, y: y });
    },
    pickAttacker: (id) => {
      dispatch({ type: "SELECT", id: id });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(InGame);
