const initialState = {
  score: 0,
};

const scoreReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GREEN":
      return { score: state.score + 30  };
    case "PURPLE":
      return { score: state.score + 40 };
    case "RED":
      return { score: state.score + 50 };
    case "BOSS":
      return { score: state.score + 60 };
    default:
      return state;
  }
};

export default scoreReducer;
