import React, { PureComponent } from "react";
import green1img from "../../assets/images/green1.jpg";
import green2img from "../../assets/images/green2.jpg";
import green3img from "../../assets/images/green3.jpg";
import purple1img from "../../assets/images/purple1.jpg";
import purple2img from "../../assets/images/purple2.jpg";
import purple3img from "../../assets/images/purple3.jpg";
import red1img from "../../assets/images/red1.jpg";
import red2img from "../../assets/images/red2.jpg";
import red3img from "../../assets/images/red3.jpg";
import bossimg from "../../assets/images/boss.jpg";
import select from "../../utility/select";

class Enemy extends PureComponent {
  constructor(props) {
    super(props);
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    this.paint();
    this.enemyMoveLoop();
  }

  componentWillUnmount() {
    clearInterval(this.handle);
  }

  componentDidUpdate() {
    this.paint();
  }

  enemyMoveLoop = () => {
    this.props.enemyMove(this.props.enemies.map((el) => el.key));
    this.handle = setTimeout(this.enemyMoveLoop, 400);
  };

  paint = () => {
    const { XB, YB, XD, YD, S, enemies } = this.props;

    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");
      const green1image = new Image();
      const green2image = new Image();
      const green3image = new Image();
      green1image.src = green1img;
      green2image.src = green2img;
      green3image.src = green3img;
      const purple1image = new Image();
      const purple2image = new Image();
      const purple3image = new Image();
      purple1image.src = purple1img;
      purple2image.src = purple2img;
      purple3image.src = purple3img;
      const red1image = new Image();
      const red2image = new Image();
      const red3image = new Image();
      red1image.src = red1img;
      red2image.src = red2img;
      red3image.src = red3img;
      const bossimage = new Image();
      bossimage.src = bossimg;

      green1image.onload = () => {
        context.clearRect(0, 0, 600, 400);
        enemies.forEach((enemy) => {
          if (enemy) {
            switch (Math.floor((enemy.x - XB) / XD) % 3) {
              //case 0: line 1,4,7,10
              //case 1: line 3,6,9
              //case 2: line 2,5,8
              case 0:
                switch (
                  (enemy.x - XB - Math.floor((enemy.x - XB) / XD) * XD) /
                  S
                ) {
                  //case 0 - x : x is the times enemy moved
                  case 1:
                  case 3:
                  case 5:
                  case 7:
                  case 9:
                  case 11:
                  case 13:
                    //2
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green2image,
                      purple2image,
                      red2image
                    );
                    break;
                  case 2:
                  case 6:
                  case 10:
                    //1
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green1image,
                      purple1image,
                      red1image
                    );
                    break;
                  case 4:
                  case 8:
                  case 12:
                  case 0:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green3image,
                      purple3image,
                      red3image
                    );
                }
                break;
              case 1:
                switch (
                  (enemy.x - XB - Math.floor((enemy.x - XB) / XD) * XD) /
                  S
                ) {
                  case 0:
                  case 2:
                  case 4:
                  case 6:
                  case 8:
                  case 10:
                  case 12:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green2image,
                      purple2image,
                      red2image
                    );
                    //2
                    break;
                  case 1:
                  case 5:
                  case 9:
                  case 13:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green1image,
                      purple1image,
                      red1image
                    );
                    //1
                    break;
                  case 3:
                  case 7:
                  case 11:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green3image,
                      purple3image,
                      red3image
                    );
                }
                break;
              case 2:
                switch (
                  (enemy.x - XB - Math.floor((enemy.x - XB) / XD) * XD) /
                  S
                ) {
                  case 0:
                  case 2:
                  case 4:
                  case 6:
                  case 8:
                  case 10:
                  case 12:
                    //2
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green2image,
                      purple2image,
                      red2image
                    );
                    break;
                  case 1:
                  case 5:
                  case 9:
                  case 13:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green3image,
                      purple3image,
                      red3image
                    );
                    //3
                    break;
                  case 3:
                  case 7:
                  case 11:
                    select(
                      enemy.x,
                      enemy.y,
                      context,
                      YB,
                      YD,
                      green1image,
                      purple1image,
                      red1image
                    );
                }
            }
            if (enemy.y === YB) {
              context.drawImage(bossimage, enemy.x, enemy.y);
            }
          }
        });
      };
    }
  };

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"enemy"}
        ref={this.canvasRef}
        width={width}
        height={height}
        style={{ zIndex: 0, position: "fixed", top: 0, left: 0 }}
      />
    );
  }
}

export default Enemy;
