import React, { PureComponent } from "react";
import bulletimg from "../../assets/images/bullet.jpg";
import music from "../../utility/music";
import e4 from "../../assets/images/e4.jpg";
import e3 from "../../assets/images/e3.jpg";
import e2 from "../../assets/images/e2.jpg";
import e1 from "../../assets/images/e1.jpg";

class Bullet extends PureComponent {
  constructor(props) {
    super(props);
    this.keyState = {};
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    this.paint();
    window.addEventListener("keydown", this.attack);
  }
  componentDidUpdate() {
    this.paint();
  }

  componentWillUnmount() {
    this.aniStatus = false;
    window.removeEventListener("keydown", this.attack);
  }

  attack = (e) => {
    if (e.code === "Space") {
      music.attack();
      //remember firing x cordinate
      this.bulletx = this.props.shipx + 14;
      window.removeEventListener("keydown", this.attack);
      this.attackLoop();
    }
  };

  collisionDetect() {
    this.props.enemies.forEach((enemy) => {
      if (enemy && !enemy.moving) {
        if (
          this.props.bullety > enemy.y &&
          this.props.bullety < enemy.y + 14 &&
          this.bulletx > enemy.x - 1 &&
          this.bulletx < enemy.x + 27
        ) {
          // &&enemy.status === 1)
          // this.props.explode(id);

          music.ememyboom();
          switch (enemy.y) {
            case this.props.YB + 3 * this.props.YD:
            case this.props.YB + 4 * this.props.YD:
            case this.props.YB + 5 * this.props.YD:
              this.props.goal("GREEN");
              break;
            case this.props.YB + 2 * this.props.YD:
              this.props.goal("PURPLE");
              break;
            case this.props.YB + 1 * this.props.YD:
              this.props.goal("RED");
              break;
            case this.props.YB + 0 * this.props.YD:
              this.props.goal("BOSS");
              break;
            default:
          }
          this.props.vanish(enemy.key);
          this.explode1 = { id: enemy.key, x: enemy.x, y: enemy.y };
          setTimeout(() => {
            this.explode2 = { ...this.explode1 };
            setTimeout(() => {
              this.explode3 = { ...this.explode2 };
              setTimeout(() => {
                this.explode4 = { ...this.explode3 };
                setTimeout(() => {
                  this.explode4 = null;
                }, 50);
                this.explode3 = null;
              }, 50);
              this.explode2 = null;
            }, 50);
            this.explode1 = null;
          }, 50);
        }
      }
    });
  }

  attackLoop = () => {
    //dispatch action
    this.props.attack();
    this.collisionDetect();

    if (this.props.bulletStatus) {
      if (this.props.bullety > -12) {
        requestAnimationFrame(this.attackLoop);
      }
      if (this.props.bullety <= -12) {
        this.props.attack();
        window.addEventListener("keydown", this.attack);
      }
    } else {
      window.addEventListener("keydown", this.attack);
    }
  };

  paint = () => {
    const { bullety, shipx } = this.props;
    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");
      const bulletimage = new Image();
      bulletimage.src = bulletimg;
      const e4image = new Image();
      e4image.src = e4;
      const e3image = new Image();
      e3image.src = e3;
      const e2image = new Image();
      e2image.src = e2;
      const e1image = new Image();
      e1image.src = e1;
      bulletimage.onload = () => {
        context.clearRect(0, 0, 600, 400);
        context.drawImage(
          bulletimage,
          bullety === 353 ? shipx + 14 : this.bulletx,
          bullety,
          2,
          7
        );
        if (this.explode1) {
          context.drawImage(e1image, this.explode1.x, this.explode1.y - 3);
          requestAnimationFrame(this.paint);
        }
        if (this.explode2) {
          context.drawImage(e2image, this.explode2.x, this.explode2.y);
          requestAnimationFrame(this.paint);
        }
        if (this.explode3) {
          context.drawImage(e3image, this.explode3.x - 3, this.explode3.y - 3);
          requestAnimationFrame(this.paint);
        }
        if (this.explode4) {
          context.drawImage(e4image, this.explode4.x - 3, this.explode4.y - 8);
          requestAnimationFrame(this.paint);
        }
      };
    }
    // if (this.aniStatus) {
    //
    // }
  };

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"bullet"}
        ref={this.canvasRef}
        width={width}
        height={height}
        style={{ zIndex: 0, position: "fixed", top: 0, left: 0 }}
      />
    );
  }
}

export default Bullet;
