import { combineReducers } from "redux";
import bgReducer from "./reducers/background";
import logoReducer from "./reducers/logo";
import shipReducer from "./reducers/ship";
import bulletReducer from "./reducers/bullet";
import enemyReducer from "./reducers/enemy/enemy";
import scoreReducer from "./reducers/score";

const reducer = combineReducers({
   bgReducer,
   logoReducer,
   shipReducer,
   bulletReducer,
   enemyReducer,
   scoreReducer
});


console.dir(reducer);
export default reducer;
